ckname != ''){\r\n            return this.attr.nickname\r\n          }else {\r\n            return ''\r\n          }\r\n          },\r\n        customerBalance(){\r\n             return this.attr.customerBalance == null ? '普通会员' : this.attr.customerBalance.level\r\n        },\r\n        cardBalance () {\r\n          return this.attr.customerBalance == null? '0':this.attr.customerBalance.balance == null ? '':this.attr.customerBalance.balance/100\r\n        },\r\n        currentDiscount () {\r\n          return this.attr.customerBalance == null ? '1.0': this.attr.customerBalance.currentDiscount\r\n        },\r\n\r\n        discountRate() {\r\n          return this.attr.customerBalance == null ? '0' : this.attr.customerBalance.discountRate\r\n        }\r\n      },\r\n      mounted: function () {\r\n        this.getAllPurchasingPlan(this.pageInfo.pageNum)\r\n      },\r\n      // 过滤器\r\n      filters:{\r\n        formatStatus: function(value){\r\n          if(value == 1){\r\n            return '充值';\r\n          }else if(value == -1){\r\n            return '消费';\r\n          }else if(value == 2){\r\n            return '首冲'\r\n          }else{\r\n            return '';\r\n          }\r\n        },\r\n\r\n        formatSource: function(value) {\r\n          if(value == 1){\r\n            return '用户端微信支付'\r\n          }else if(value == 2){\r\n            return '会员卡收款'\r\n          }else if(value == 3){\r\n            return '其他'\r\n          }else if(value == 4) {\r\n            return '用户端微信支付充值'\r\n          }\r\n          else if(value == 5){\r\n            return 'pos收款';\r\n          }else if(value == 6){\r\n            return '现金收款';\r\n          }else if(value == 7){\r\n            return '大众点评收款'\r\n          }else if(value == 8){\r\n            return '微信扫码支付';\r\n          }else if(value == 9){\r\n            return '团购'\r\n          }else if(value == 10) {\r\n            return '免单'\r\n          }else if(value == 11) {\r\n            return '会员帮付'\r\n          }